<template>
<div id="page-top">
  <!-- Page Wrapper -->
  <div id="wrapper">
    <ul class="navbar-nav bg-gradient-primary sidebar sidebar-dark accordion" id="accordionSidebar">
      <!-- Sidebar - Brand -->
      <div class="sidebar-brand d-flex align-items-center justify-content-center">
        <div class="sidebar-brand-icon rotate-n-15">
          <i class="fas fa-laugh-wink"></i>
        </div>
        <div class="sidebar-brand-text mx-3">EBOOK</div>
      </div>
      <!-- Divider -->
      <hr class="sidebar-divider my-0">
      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Book Table</span>
        </router-link>
      </li>

      <hr class="sidebar-divider my-0">
      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/login' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Log out</span>
        </router-link>
      </li>
    </ul>
    <!-- Content Wrapper -->
    <div id="content-wrapper" class="d-flex flex-column">

      <!-- Main Content -->
      <div id="content">

        <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">

          <ul class="navbar-nav ml-auto">
            <li class="nav-item dropdown no-arrow d-sm-none">
              <a class="nav-link dropdown-toggle" href="#" id="searchDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                <i class="fas fa-search fa-fw"></i>
              </a>
              <!-- Dropdown - Messages -->
              <div class="dropdown-menu dropdown-menu-right p-3 shadow animated--grow-in" aria-labelledby="searchDropdown">
                <form class="form-inline mr-auto w-100 navbar-search">
                  <div class="input-group">
                    <input type="text" class="form-control bg-light border-0 small" placeholder="Search for..." aria-label="Search" aria-describedby="basic-addon2">
                    <div class="input-group-append">
                      <button class="btn btn-primary" type="button">
                        <i class="fas fa-search fa-sm"></i>
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </li>
          </ul>
        </nav>

        <div class="container-fluid">
          <!-- <button class="btn btn-primary btn-user" v-on:click="handleUserClick()">用户浏览</button> -->

          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">Carts</h6>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <div>Filter cart:</div>
                <input class="form-control bg-light border-0 small" v-model="filters.name.value"/>

                <div>
                  <div>
                    <v-table
                        :data="tableData"
                        :filters="filters"
                        selectionMode="multiple"
                        selectedClass="table-info"
                        @selectionChanged="selectedRows = $event"
                        class="table table-bordered"
                        id="dataTable"
                        width="100%"
                        cellspacing="0"
                    >
                      <thead slot="head">
                        <th>BookName</th>
                        <th>Amount</th>
                        <th>Paied</th>
                      </thead>
                      <tbody slot="body" slot-scope="{displayData}">
                        <v-tr
                            v-for="row in displayData"
                            :key="row.id"
                            :row="row"
                        >
                          <td>{{ row.bookname }}</td>
                          <td>{{ row.amount }}</td>
                          <td>{{ row.paied }}</td>
                        </v-tr>
                      </tbody>
                    </v-table>
                    <strong>Selected:</strong>
                    <div v-if="selectedRows.length === 0" class="text-muted">No Rows Selected</div>
                    <ul>
                      <li v-for="selected in selectedRows" :key="selected.id">
                        {{ selected.bookname }}
                      </li>
                    </ul>
                    <div>
                      <div>
                        <button class="btn btn-primary btn-user" v-on:click="handleBuyClick()">购买</button>
                        <button class="btn btn-primary btn-user" v-on:click="handleDeleteClick()">删除</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- End of Main Content -->
      <!-- Footer -->
      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; EBOOK Store 2019</span>
          </div>
        </div>
      </footer>
    </div>
  </div>
  <a class="scroll-to-top rounded" href="#page-top">
    <i class="fas fa-angle-up"></i>
  </a>
  <div class="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Ready to Leave?</h5>
          <button class="close" type="button" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">×</span>
          </button>
        </div>
        <div class="modal-body">Select "Logout" below if you are ready to end your current session.</div>
        <div class="modal-footer">
          <button class="btn btn-secondary" type="button" data-dismiss="modal">Cancel</button>
          <a class="btn btn-primary" href="login.html">Logout</a>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'

var ta = [
  {
    bookname: 'Harry Potter',
    amount: 1,
    paied: 0
  },
  {
    bookname: 'King of the Ring',
    amount: 2,
    paied: 0
  },
  {
    bookname: 'The Three-Body Problem',
    amount: 1,
    paied: 1
  }
]

export default {
  name: 'table',
  data () {
    return {
      useremail: '125343@163.com',
      showDialog: true,
      loading: true,
      // tableData: [],
      tableData: ta,
      currentPage: 1,
      totalPage: 0,
      selectedRows: [],
      filters: {
        name: { value: '', keys: ['bookname', 'amount', 'paied'] }
      }
    }
  },
  methods: {
    getCarts () {
      http
        .post('/cart', this.useremail)
        .then(response => {
          this.tableData = response.data
        }).finally(() => {
          this.loading = !this.loading
        })
        .catch(e => {
          console.log(e)
        })
    },
    handleBuyClick () {
      var email = this.useremail
      this.selectedRows.forEach(function (value) {
        var data = {
          useremail: email,
          bookname: value.bookname,
          amount: value.amount,
          paied: true
        }
        // alert(value.bookname)
        // alert(data.useremail)
        http
          .post('/modifyCart', data)
          .then(response => {
            this.getCarts()
          }).finally(() => {
            this.loading = !this.loading
          })
          .catch(e => {
            console.log(e)
          })
      })
      this.selectedRows = []
    },
    handleDeleteClick () {
      var email = this.useremail
      this.selectedRows.forEach(function (value) {
        var data = {
          useremail: email,
          bookname: value.bookname,
          amount: value.amount,
          paied: value.paied
        }
        http
          .post('/deletecart', data)
          .then(response => {
            this.getCarts()
          }).finally(() => {
            this.loading = !this.loading
          })
          .catch(e => {
            console.log(e)
          })
      })
      this.selectedRows = []
    }
  },
  mounted () {
    http
      .post('/cart', this.useremail)
      .then(response => {
        this.tableData = response.data
      }).finally(() => {
        this.loading = !this.loading
      })
      .catch(e => {
        console.log(e)
      })
  }
}
</script>

<style>

</style>



