<template>
<div id="page-top">
  <!-- Page Wrapper -->
  <div id="wrapper">
    <!-- Sidebar -->
    <ul class="navbar-nav bg-gradient-primary sidebar sidebar-dark accordion" id="accordionSidebar">
      <!-- Sidebar - Brand -->
      <a class="sidebar-brand d-flex align-items-center justify-content-center" href="index.html">
        <div class="sidebar-brand-icon rotate-n-15">
          <i class="fas fa-laugh-wink"></i>
        </div>
        <div class="sidebar-brand-text mx-3">EBOOK</div>
      </a>
      <!-- Divider -->
      <hr class="sidebar-divider my-0">

      <!-- Nav Item - Dashboard -->
      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/login'}">
          <i class="fas fa-fw fa-tachometer-alt"></i>
          <span>Log In</span>
        </router-link>
      </li>

      <!-- Divider -->
      <hr class="sidebar-divider">

      <!-- Heading -->
      <li class="nav-item">
        <a class="nav-link" href="charts.html">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>文艺</span></a>
      </li>

      <!-- Nav Item - Pages Collapse Menu -->
      <li class="nav-item">
        <a class="nav-link collapsed" href="#" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="true" aria-controls="collapseTwo">
          <i class="fas fa-fw fa-cog"></i>
          <span>童书</span>
        </a>
      </li>

      <!-- Nav Item - Utilities Collapse Menu -->
      <li class="nav-item">
        <a class="nav-link collapsed" href="#" data-toggle="collapse" data-target="#collapseUtilities" aria-expanded="true" aria-controls="collapseUtilities">
          <i class="fas fa-fw fa-wrench"></i>
          <span>人文社科</span>
        </a>
      </li>

      <!-- Divider -->
      <hr class="sidebar-divider">

      <!-- Heading -->
      <li class="nav-item">
        <a class="nav-link collapsed" href="#" data-toggle="collapse" data-target="#collapsePages" aria-expanded="true" aria-controls="collapsePages">
          <i class="fas fa-fw fa-folder"></i>
          <span>生活</span>
        </a>
      </li>

      <!-- Nav Item - Pages Collapse Menu -->
      <li class="nav-item">
        <a class="nav-link collapsed" href="#" data-toggle="collapse" data-target="#collapsePages" aria-expanded="true" aria-controls="collapsePages">
          <i class="fas fa-fw fa-folder"></i>
          <span>科技</span>
        </a>
      </li>

      <!-- Divider -->
      <hr class="sidebar-divider d-none d-md-block">

      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/cart' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Shop Cart</span>
        </router-link>
      </li>

      <hr class="sidebar-divider d-none d-md-block">

      <!-- Nav Item - Charts -->
      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/login' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Log out</span>
        </router-link>
      </li>

      <!-- Sidebar Toggler (Sidebar) -->
      <div class="text-center d-none d-md-inline">
        <button class="rounded-circle border-0" id="sidebarToggle"></button>
      </div>

    </ul>
    <!-- End of Sidebar -->

    <!-- Content Wrapper -->
    <div id="content-wrapper" class="d-flex flex-column">

      <!-- Main Content -->
      <div id="content">

        <!-- Topbar -->
        <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">

          <!-- Sidebar Toggle (Topbar) -->
          <button id="sidebarToggleTop" class="btn btn-link d-md-none rounded-circle mr-3">
            <i class="fa fa-bars"></i>
          </button>
        </nav>
        <!-- End of Topbar -->

        <!-- Begin Page Content -->
        <div class="container-fluid">

          <!-- Page Heading -->
          <h1 class="h3 mb-2 text-gray-800">Books</h1>
          <!-- <button class="btn btn-primary btn-user btn-block" v-on:click="handleBookClick()">图书浏览</button> -->
          <!-- <p class="mb-4">DataTables is a third party plugin that is used to generate the demo table below. For more information about DataTables, please visit the <a target="_blank" href="https://datatables.net">official DataTables documentation</a>.</p> -->

          <!-- DataTales Example -->
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">Book Store</h6>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <label>Filter Books:</label>
                <input class="form-control bg-light border-0 small" v-model="filters.name.value"/>

                <div>
                  <div>
                    <v-table
                        :data="tableData"
                        :filters="filters"
                        selectionMode="multiple"
                        selectedClass="table-info"
                        @selectionChanged="selectedRows = $event"
                        class="table table-bordered"
                        id="dataTable"
                        width="100%"
                        cellspacing="0"
                    >
                      <thead slot="head">
                        <th>Name</th>
                        <th>Writer</th>
                        <th>Price</th>
                        <th>ISBN</th>
                        <th>Inventory</th>
                        <th>Action</th>
                      </thead>
                      <tbody slot="body" slot-scope="{displayData}">
                        <v-tr
                            v-for="row in displayData"
                            :key="row.id"
                            :row="row"
                        >
                          <td>{{ row.name }}</td>
                          <td>{{ row.writer }}</td>
                          <td>{{ row.price }}</td>
                          <td>{{ row.isbn }}</td>
                          <td>{{ row.inventory }}</td>
                          <td>
                            <router-link :to="{path: '/about'}">show details</router-link>
                          </td>
                        </v-tr>
                      </tbody>
                    </v-table>
                    <strong>Selected:</strong>
                    <div v-if="selectedRows.length === 0" class="text-muted">No Rows Selected</div>
                    <ul>
                      <li v-for="selected in selectedRows" :key="selected.id">
                        {{ selected.name }}
                      </li>
                    </ul>
                    <button class="btn btn-primary btn-user" v-on:click="handleAddClick()">加入购物车</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>

      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; EBOOK Store 2019</span>
          </div>
        </div>
      </footer>
    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'
// import axios from 'axios'

// var ta = [
//   {
//     name: 'Harry Potter',
//     writer: 'J. K. Rowling',
//     price: 30,
//     isbn: '‎978-3-16-148410-0',
//     inventory: 20
//   },
//   {
//     name: 'King of the Ring',
//     writer: 'John Ronald Reuel Tolkien',
//     price: 50,
//     isbn: '178-3-16-148410-0',
//     inventory: 9
//   },
//   {
//     name: 'The Three-Body Problem',
//     writer: 'Liu Cixin',
//     price: 40,
//     isbn: '278-3-16-148410-0',
//     inventory: 7
//   }
// ]

export default {
  name: 'table',
  data () {
    return {
      useremail: '174783@qq.com',
      loading: true,
      tableData: [],
      currentPage: 1,
      totalPages: 2,
      selectedRows: [],
      filters: {
        name: { value: '', keys: ['name', 'writer', 'price', 'isbn', 'inventory'] }
      }
    }
  },
  methods: {
    handleAddClick () {
      var email = this.useremail
      this.selectedRows.forEach(function (value) {
        var data = {
          useremail: email,
          bookname: value.name,
          amount: 1,
          paied: false
        }
        http
          .post('/addcart', data)
          .then(response => {
            console.log(this.selectedRows)
          })
          .catch(e => {
            console.log(e)
          })
      })
    }
  },
  mounted () {
    http
      .get('/book')
      .then(response => {
        this.tableData = response.data
        // this.tableData = ta
      }).finally(() => {
        this.loading = false
      })
      .catch(e => {
        console.log(e)
      })
  }
}
</script>

<style>

</style>



