<template>
<div class="bg-gradient-primary">
  <div class="container">
    <!-- Outer Row -->
    <div class="row justify-content-center">
      <div class="col-xl-10 col-lg-12 col-md-9">
        <div class="card o-hidden border-0 shadow-lg my-5">
          <div class="card-body p-0">
            <!-- Nested Row within Card Body -->
            <div class="row">
              <div class="col-lg-6 d-none d-lg-block bg-login-image"></div>
              <div class="col-lg-6">
                <div class="p-5">
                  <div class="text-center">
                    <h1 class="h4 text-gray-900 mb-4">Welcome Back!</h1>
                  </div>
                  <form class="user">
                    <div class="form-group">
                      <input type="email" class="form-control form-control-user" id="exampleInputEmail" required v-model="user.email" aria-describedby="emailHelp" placeholder="Enter Email Address...">
                    </div>
                    <div class="form-group">
                      <input type="password" class="form-control form-control-user" id="exampleInputPassword" required v-model="user.password" placeholder="Password">
                    </div>
                    <div class="form-group">
                      <div class="custom-control custom-checkbox small">
                        <input type="checkbox" class="custom-control-input" id="customCheck">
                        <label class="custom-control-label" for="customCheck">Remember Me</label>
                      </div>
                    </div>
                    <div>
                      <button class="btn btn-primary btn-user btn-block" v-on:click="handleLoginClick()">
                        Login</button>
                    </div>
                    <hr>
                    <a href="index.html" class="btn btn-google btn-user btn-block">
                      <i class="fab fa-google fa-fw"></i> Login with Google
                    </a>
                    <a href="index.html" class="btn btn-facebook btn-user btn-block">
                      <i class="fab fa-facebook-f fa-fw"></i> Login with Facebook
                    </a>
                  </form>
                  <hr>
                  <div class="text-center">
                    <a class="small"><router-link :to="{path:'/forgetpw'}">Forgot Password?</router-link></a>
                  </div>
                  <div class="text-center">
                    <a class="small"><router-link :to="{path:'/register'}">Create an Account!</router-link></a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'
import router from '../../router/index.js'
// import axios from 'axios'

export default {
  name: 'login',
  data () {
    return {
      user: {
      },
      loading: true,
      exist: true,
      newPath: null
      // submitted: false
    }
  },
  methods: {
    handleLoginClick () {
      var data = {
        firstname: '',
        lastname: '',
        email: this.user.email,
        password: this.user.password,
        active: true
      }
      http
        .post('/login', data)
        .then(response => {
          this.exist = response.data
          this.newPath = (this.exist === true) ? '/table' : '/'
          alert(this.exist)
          alert(this.newPath)
          router.push({ path: this.newPath })
        }).finally(() => {
          this.loading = false
        })
        .catch(e => {
          console.log(e)
        })
      // this.submitted = true
    }
  }
  // ,
  // mounted () {
  //   axios
  //     .post('/login')
  //     .then(response => (
  //       router.push({ path: 'table' })
  //     ))
  //     .catch(e => {
  //       console.log(e)
  //     }
  //     )
  // }
}
</script>

<style >

</style>



